.community_page {
  display: flex;
  width: 100%;
  height: 100%;
  background-color: #fff;
}

.community_page .community_frame {
  display: flex;
  width: 100%;
  height: 100%;
}

.community_page .chat_frame {
  width: 30%;
  height: 600px;

  display: flex;
  flex-direction: column;
  background-color: #182226;
  border: 0.5px solid gray;
  border-radius: 34px;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px,
    rgba(0, 0, 0, 0.3) 0px 30px 60px -30px,
    rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
}

.community_page .community_all_frame {
  width: 68%;
  height: auto;
}

.community_page .chat_frame .chat_top {
  width: 100%;
  height: 94px;

  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom: 1px solid rgba(228, 228, 228, 0.83);

  font-family: "Helvetica";
  font-style: normal;
  font-weight: 600;
  font-size: 36px;
  line-height: 54px;
  color: #fff;
}

.community_page .chat_content {
  position: relative;
  padding: 17px;
  width: 100%;
  height: 100%;

  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  overflow: hidden;
  background: #fff;
}

.community_page .chat_content .chat_main {
  position: relative;
  /*padding: 17px;*/
  width: 100%;
  /*height: 100%;*/

  display: flex;
  flex-direction: column;
  overflow-y: scroll;
}

.community_page .chat_frame .chat_bott {
  width: 100%;
  height: 94px;

  display: flex;
  justify-content: center;
  align-items: center;
  border-top: 1px solid rgba(228, 228, 228, 0.83);
}

.community_page .chat_bott .msg_input {
  padding: 20px;
  width: 276px;
  height: 50px;

  display: flex;
  flex-direction: column;
  align-items: center;
  background: #f7f7f7;
  border-radius: 28px;
  border: none;
  outline-color: #257677;

  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
}

.community_page .chat_bott .send_msg_btn {
  margin-left: 14px;
  width: 50px;
  height: 50px;

  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  background: linear-gradient(180deg, #55a99d 0%, #007665 100%);
  border: none;
}

.community_page .chat_main .msg_left {
  margin-left: 10px;
  padding: 9px;
  width: auto;
  height: auto;

  display: flex;
  background: rgba(228, 228, 228, 0.83);
  border-radius: 20px 20px 20px 0px;
}

.community_page .msg_right {
  padding: 9px;
  width: auto;
  height: auto;

  display: flex;
  background: #d0ece8;
  border-radius: 20px 20px 0px 20px;
}

.community_all_frame .article_tabs {
  width: 100%;
  height: 94px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.article_main {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.community_page .all_article_box {
  width: 90%;
  height: 170px;
  margin-top: 15px;
  display: flex;
  flex-direction: row;
  align-items: center;
  background: #8675a9;
  box-shadow: 0px 4px 40px rgb(0 0 0 / 25%);
  border-radius: 30px;
}

.community_page .all_article_box .all_article_img {
  width: 155px;
  height: 155px;
  border-radius: 25px;
  /* background: url(https://images.unsplash.com/photo-1546069901-ba9599a7e63c?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=3160&q=80); */
  background-size: cover;
  margin-left: 5px;
}

.community_page .all_article_box .all_article_container {
  margin-left: 15px;
  width: 75%;
  height: 90%;
  display: flex;
  flex-direction: column;
}

.community_page .all_article_author_user {
  margin-left: 10px;
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 26px;
  color: #ffffff;
}

.community_page .all_article_title {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 600;
  font-size: 22px;
  line-height: 33px;
  color: #e3c08d;
}

.community_page .all_article_desc {
  margin-top: 5px;
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 26px;
  color: #ffffff;
}

.community_page .article_share {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}

.community_page .article_bott {
  width: 100%;
  height: 90px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  border-top: 1px solid rgba(228, 228, 228, 0.83);
}
