.order_page {
  width: 100%;
  height: auto;
  background: #f3f9fb;

  display: flex;
}

.order_page .order_left {
  width: 70%;
  height: auto;
}

.order_page .order_right {
  margin-top: 86px;
  margin-left: 30px;
  width: 30%;
  height: auto;
  display: flex;
  flex-direction: column;
}

.order_page .order_left .order_nav_frame {
  width: 100%;
  height: 60px;

  display: flex;
  flex-direction: row;
  justify-content: center;
  border-bottom: 1px solid #ffffff;
}

.order_page .order_right .order_info_box {
  padding: 20px;
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: #8675a9;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  border-radius: 10px;
}

.order_page .order_right .order_payment_box {
  padding: 20px;
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  background: #ffffff;

  border-radius: 10px;
}

.order_page .order_info_box .order_user_img {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.order_page .order_info_box .order_user_img .order_user_avatar {
  width: 100px;
  height: 100px;
  border: 1px solid #fff;
  border-radius: 90px;
}

.order_page .order_info_box .order_user_name {
  margin-top: 10px;
  font-family: "Helvetica";
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 36px;
  color: #f3f9fb;
}

.order_page .order_info_box .order_user_prof {
  font-family: "Helvetica";
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 36px;
  color: #f3f9fb;
}

.order_page .order_info_box .order_user_address {
  margin-top: 10px;
  width: 90%;
  display: flex;
  flex-direction: row;
  color: #fff;
}

.order_page .order_info_box .card_input {
  margin-top: 10px;
  padding: 10px;
  width: 100%;
  height: 40px;
  background: #f5f5f5;
  border-radius: 10px;
  font-family: "Nunito";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  border: 0.5px solid #b3a6a6;
  letter-spacing: -0.02em;
  color: #434141;
}

.order_page .order_info_box .card_half_input {
  margin-top: 10px;
  padding: 10px;
  width: 49%;
  height: 40px;
  background: #f5f5f5;
  border-radius: 10px;
  font-family: "Nunito";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  border: 0.5px solid #b3a6a6;
  letter-spacing: -0.02em;
  color: #434141;
}

.order_page .order_info_box .card_input {
  margin-top: 10px;
  padding: 10px;
  width: 100%;
  height: 40px;
  background: #f5f5f5;
  border-radius: 10px;
  font-family: "Nunito";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  border: 0.5px solid #b3a6a6;
  letter-spacing: -0.02em;
  color: #434141;
}

.order_page .order_info_box .cards_box {
  margin-top: 35px;
  width: 80%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.order_page .order_info_box .cards_box img {
  width: 39px;
  height: 26px;
}

.order_page .order_info_box .spec_address_txt {
  margin-left: 5px;
  font-family: "DM Sans";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #fff;
}

.order_page .css-1gsv261 {
  width: 75%;
}

.order_page .css-heg063-MuiTabs-flexContainer {
  justify-content: space-between;
}

.order_page .order_main_content {
  width: 100%;
  height: auto;
}

.order_page .order_main_content .order_main_box {
  position: relative;
  width: 100%;
  max-height: 300px;
  margin-bottom: 25px;

  background: #ffffff;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  border-radius: 10px;
}

.order_page .order_main_content .order_main_box .order_box_scroll {
  position: relative;
  overflow: hidden;
  width: 100%;
  max-height: 300px;
  margin-top: 15px;

  display: flex;
  flex-direction: column;
  align-items: center;
  overflow-y: scroll;
}

.order_page .order_box_scroll .ordersName_price {
  position: relative;
  width: 100%;
  height: 47px;
  margin-top: 5px;
  margin-left: 40px;

  display: flex;
  flex-direction: row;
  align-items: center;
}

.order_page .order_box_scroll .ordersName_price .orderDishImg {
  position: relative;
  height: 45px;
  width: 45px;

  border-radius: 15px;
  background-size: cover;
}

.order_page .order_box_scroll .ordersName_price .titleDish {
  position: relative;
  width: 260px;
  height: 36px;
  margin-left: 20px;

  font-family: "Helvetica";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  /* identical to box height, or 164% */

  color: #144272;
}

.order_page .order_box_scroll .ordersName_price .priceBox {
  position: relative;
  max-width: 170px;
  height: 100%;
  margin-left: 70px;

  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.order_page .order_box_scroll .ordersName_price .priceBox p {
  font-family: "Helvetica";
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 27px;
  /* identical to box height */

  color: gray;
}

.order_page .order_main_content .order_main_box .total_price_box {
  position: relative;
  width: 100%;
  height: 55px;
  margin-top: 15px;
  margin-bottom: 5px;

  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
}

.order_page .order_main_content .order_main_box .total_price_box .boxTotal {
  position: relative;
  max-width: 535px;
  height: 100%;

  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.order_page .total_price_box .boxTotal p {
  margin-left: 10px;
  font-family: "Helvetica";
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 24px;
  color: #fd8a8a;
}

.order_page .total_price_box .boxTotal .data_compl {
  /*margin-left: 20px;*/
  font-family: "Commissioner";
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 36px;
  /* or 257% */

  color: #000000;
}
